::-webkit-scrollbar-button {
  display: block;
  height: 13px;
  border-radius: 0px;
  background-color: #d9d9d9;
}

::-webkit-scrollbar-button:hover {
  background-color: #d9d9d9;
}

::-webkit-scrollbar-thumb {
  background-color: #b71717;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #8a1111;
}

::-webkit-scrollbar-track {
  background-color: #f2f2f2;
}

::-webkit-scrollbar-track:hover {
  background-color: #e6e6e6;
}

::-webkit-scrollbar {
  width: 4px;
}

::selection {
  background: #e31e1e;
}

::-moz-selection {
  background: #e31e1e;
}

@media all and (-ms-high-contrast: none) {
  html {
    display: flex;
    flex-direction: column;
  }
}

#app-wrapper {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  min-height: 100vh;
}

#main {
  flex: 1;
  margin-top: calc(48px + 0.5rem);
  font-size: 0.875rem;
  line-height: 1.5;
  transition: all 0.3s;
}

.content {
  padding: 16px 16px;
  min-width: 0;
}

.sidebar-fixed #main {
  margin-left: 230px;
  margin-top: calc(48px + 0.5rem);
}

@media (max-width: 991.98px) {
  .sidebar-fixed #main {
    margin-left: 0;
  }
}

.sidebar-hidden #main {
  margin-left: 0;
}

.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.pace .pace-progress {
  background: #b71717;
  position: fixed;
  z-index: 2;
  top: calc(48px + 0.5rem);
  right: 100%;
  width: 100%;
  height: 2px;
}

.embedded-layout .pace .pace-progress {
  top: 0px;
}

/**
 * Header
 */
#sidebar {
  display: flex;
  flex-direction: column;
  background: #f2f2f2;
  width: 230px;
  height: 100vh;
  flex-shrink: 0;
  transition: all 0.3s;
  overflow: hidden;
  border-right: 1px solid #dee2e6;
}

#sidebar .sidebar-nav {
  flex: 1;
  position: relative;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background: inherit;
  border: none;
  margin-top: calc(48px + 0.5rem);
}

#sidebar .nav {
  width: 100%;
  list-style: none;
  background: inherit;
}

#sidebar .sidebar-categories {
  width: 100%;
}

#sidebar .sidebar-categories input.form-control {
  margin-left: 10px;
  margin-bottom: 1em;
  width: auto;
}

#sidebar .nav-item {
  width: 100%;
  position: relative;
}

#sidebar .nav-link {
  display: block;
  text-decoration: none;
  padding: 14px 20px;
  color: #0d0d0d;
  font-size: 14px;
}

#sidebar .nav-link i:first-of-type {
  display: inline-block;
  width: 24px;
  margin-right: 4px;
  color: #666666;
  font-size: 14px;
}

#sidebar .nav-link:hover {
  background: #bfbfbf;
  color: #0d0d0d;
}

#sidebar .nav-link:hover i {
  color: #0d0d0d;
}

#sidebar .nav-link.active {
  color: #b71717;
  background: #cccccc;
}

#sidebar .nav-link.active:hover {
  background: #bfbfbf;
}

#sidebar .nav-link.active i {
  color: #b71717;
}

#sidebar .nav-dropdown-toggle i:last-of-type {
  float: right;
  margin-top: 4px;
}

#sidebar .nav-title {
  font-size: 80%;
  text-transform: uppercase;
  padding: 0.8rem 1rem;
  color: #333333;
}

#sidebar .nav-dropdown.open .nav-dropdown-items {
  max-height: 1000px !important;
}

#sidebar .nav-dropdown.open .nav-dropdown-toggle i:last-of-type {
  transform: rotate(270deg);
}

#sidebar .nav-dropdown-items {
  padding: 0;
  margin: 0;
  overflow: hidden;
  max-height: 0 !important;
  transition: max-height 0.3s ease-in-out;
}

#sidebar .nav-dropdown-items .nav-item {
  padding: 0;
  list-style: none;
}

.sidebar-fixed #sidebar {
  position: fixed;
}

.sidebar-hidden #sidebar {
  margin-left: -230px;
}

@media (max-width: 991.98px) {
  .sidebar {
    margin-left: -230px;
  }
  .sidebar-mobile-show .sidebar {
    margin-left: 0;
  }
  .sidebar-categories input.form-control {
    display: none;
  }
}

@media (max-width: 991.98px) {
  #sidebar {
    margin-left: -230px;
  }
  .sidebar-mobile-show #sidebar {
    margin-left: 0;
  }
}

